.side-nav {
  position: fixed;
  right: 0;
  height: calc(100vh - 40vh);
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: flex-end;
  padding-top: 20vh;
  padding-bottom: 20vh; }
  .side-nav div {
    transition: 0.5s;
    padding: 30px; }
    .side-nav div:hover {
      transition: 0.5s; }
      .side-nav div:hover i {
        font-size: 70px; }
    .side-nav div a {
      color: #ffffff;
      text-decoration: none; }
  .side-nav i {
    transition: 0.5s;
    font-size: 40px; }

@media only screen and (max-width: 600px) {
  .side-nav {
    position: fixed;
    bottom: 0;
    width: 100vw;
    height: auto;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-around;
    align-items: center;
    padding: 0px;
    background-color: #111; }
    .side-nav div {
      padding: 10px; }
      .side-nav div:hover i {
        font-size: 30px; }
    .side-nav i {
      font-size: 30px; } }
